body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#433E46 ;
  color:#DCE0D9 ;
}

.navbar, .nav-link, .navbar-brand{
  background-color: #2c292e !important  ;
  color:#DCE0D9 !important;
  
}
 
.peep {
  max-width: 500px;
  width: 100%;
  border-radius: 50%;
  margin-bottom:30px;
}
.intro-text {
  font-size: 30px;
  text-align:center; 
  margin-top:-50px;
}

.more-text{
  text-align: center;
  font-size:20px;
  margin-top:10px;
}

.mrrobot{
  width: 100%;
  max-width: 500px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}


.gai{
  max-width: 600px;
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#typewriter-text {  
  margin-top:20px;
  margin-bottom: 100px;
  display: block;
  font-size:80px;
  text-align: center;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
